import { login } from "./../../api/login"
import { Base64 } from 'js-base64';
	export default {
		data() {
			return {
				username:"",
				password:"",
				storeList:[],
				showSelStore:false,
				token:"",
			}
		},
		onLoad() {
			uni.clearStorage();
		},
		methods: {
			toThisStore(item){
				uni.setStorage({
					key: 'storeInfo',
					data: JSON.stringify(item),
					success: function () {
						console.log('success storeInfo');
					}
				});
				uni.setStorage({
					key: 'token',
					data: JSON.stringify(this.token),
					success: function () {
						console.log('success token');
					}
				});
				uni.navigateTo({
					url: './../index/index'
				});
			},
			checkInfo(){
				if(!this.username){
					uni.showToast({
						title: "请输入用户名",
						duration: 2000,
						icon:"none"
					});
					return false;
				}
				if(!this.password){
					uni.showToast({
						title: "请输入密码",
						duration: 2000,
						icon:"none"
					});
					return false;
				}
				return true;
			},
			async doLogin(){
				let flag = this.checkInfo();
				if(!flag){
					return;
				}
				let data = {
					"username": this.username,
                    "password": this.password
				}
				let authStr = 'Basic '+ Base64.encode(data.username+':'+data.password);
				let res = await login(authStr);
				this.token = authStr;
				console.log(res)
				this.storeList = res.data;
				if(res.data.length==0){
					uni.showToast({
						title: "暂无店铺",
						duration: 2000,
						icon:"none"
					});
					this.showSelStore = false;
				}else{
					this.showSelStore = true;
				}
			}
		}
	}
